<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import EmptyResults from "@/components/EmptyResults.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
/**Default receipt */
import ReceiptOne from "@/index/shared/ReceiptOne.vue";
export default {
  name: "Sales",
  mixins: [dataTableService],
  components: { ContentHeader, EmptyResults, ReceiptOne },
  data: () => ({
    fetchUrl: "orders/sales",
    selectedOrder: null,
  }),
  methods: {
    selectOrder(order) {
      this.selectedOrder = order;
      this.toggleModal();
    },
    handleReceiptPrint(elementId) {
      console.log(elementId);
    },
    handleDestroy(orderId) {
      this.$http.get(`orders/delete/${orderId}`).then((response) => {
        if (response.data.status) {
          let index = this.rows.findIndex((item) => item.id == orderId);
          this.rows.splice(index, 1);
          this.$store.commit("SET_FLASH_MESSAGE", {
            type: "success",
            message: "Sales deleted successfully",
          });
        }
      });
    },
  },
};
</script>
<template>
  <content-header
    title="Sales"
    subtitle="List of all paid invoices and rejected ones"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap" v-if="rows.length">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div
              class="col-md-12 d-flex gap-1 align-items-center justify-content-md-end mb-3"
            >
              <div class="form-group">
                <input
                  type="text"
                  placeholder="Search..."
                  class="form-control"
                  autocomplete="off"
                />
              </div>
              <div class="btn-group">
                <button class="btn btn-icon btn-outline-light">
                  <i class="demo-pli-download-from-cloud fs-5"></i>
                </button>
                <div class="btn-group dropdown">
                  <button
                    class="btn btn-icon btn-outline-light dropdown-toggle dropdown-toggle-split"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <span class="visually-hidden">Toggle Dropdown</span>
                  </button>
                  <ul class="dropdown-menu dropdown-menu-end" style="">
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >All sales</a
                      >
                    </li>
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >Paid</a
                      >
                    </li>
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)">Due</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card-body">
          <div class="table-responsive">
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th>Order #</th>
                  <th>Waiter</th>
                  <th>Amount</th>
                  <th>Pay. Method</th>
                  <th>Client</th>
                  <th>Table</th>
                  <th>Branch</th>
                  <th>Status</th>
                  <th>Created at</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="order in rows" :key="order.reference">
                  <td>
                    <span>#{{ $helper.generateVoucherNo(order.id) }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">
                      {{
                        order.waiter ? order.waiter.name : "Administrator"
                      }}</span
                    >
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.grand_total)
                    }}</span>
                  </td>
                  <td>
                    <span>{{ order.payment_type || "N/A" }}</span>
                  </td>
                  <td>
                    <span>{{
                      order.client ? order.client.name : "Walk-in Customer"
                    }}</span>
                  </td>
                  <td>
                    <a class="btn-link" href="javascript:void(0)">{{
                      order.table.name
                    }}</a>
                  </td>
                  <td>
                    <a class="btn-link" href="javascript:void(0)">
                      {{ order.branch ? order.branch.name : "Headquarter" }}</a
                    >
                  </td>
                  <td>
                    <span class="badge bg-success" v-if="order.paid == 1"
                      >Paid</span
                    >
                    <span class="badge bg-warning" v-else>Due</span>
                  </td>
                  <td>
                    <span>{{
                      `${$helper.formatDate(
                        order.created_at
                      )} ${$helper.formatOrderTime(order.created_at)}`
                    }}</span>
                  </td>
                  <td class="text-center text-nowrap">
                    <a
                      class="btn btn-icon btn-sm btn-primary btn-hover"
                      href="javascript:void(0)"
                      ><i class="demo-pli-pen-5 fs-5"></i
                    ></a>
                    <a
                      class="btn btn-icon btn-sm btn-danger btn-hover"
                      href="javascript:void(0)"
                      @click.prevent="
                        $confirm({
                          title: 'Delete sale record',
                          content: `Are you sure you want to delete this sale #${$helper.generateVoucherNo(
                            order.id
                          )} ?`,
                          classes: 'btn btn-danger',
                          actionText: 'Yes, Continue',
                          closeText: 'Cancel',
                          action: () => {
                            handleDestroy(order.id);
                          },
                        })
                      "
                      ><i class="demo-pli-trash fs-5"></i
                    ></a>
                    <a
                      class="btn btn-icon btn-sm btn-primary btn-hover"
                      href="javascript:void(0)"
                      @click.prevent="selectOrder(order)"
                      ><svg
                        height="16px"
                        viewBox="0 0 16 16"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="currentColor"
                      >
                        <path
                          d="M11 2H5a1 1 0 0 0-1 1v2H3V3a2 2 0 0 1 2-2h6a2 2 0 0 1 2 2v2h-1V3a1 1 0 0 0-1-1zm3 4H2a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h1v1H2a2 2 0 0 1-2-2V7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-1h1a1 1 0 0 0 1-1V7a1 1 0 0 0-1-1z"
                        ></path>
                        <path
                          fill-rule="evenodd"
                          d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1zM5 8a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-3a2 2 0 0 0-2-2H5z"
                        ></path>
                        <path
                          d="M3 7.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0z"
                        ></path></svg
                    ></a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <pagination
            :from="recordsFrom"
            :to="recordsTo"
            :current_page="currentPage"
            :row_count_page="rowCountPage"
            @page-update="handlePagination"
            :total_records="totalRows"
            :page_range="pageRange"
          ></pagination>
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-center" style="min-height: 93vh" v-else>
      <empty-results>
        <template #description
          ><span>No records found. Please wait for new orders</span></template
        >
      </empty-results>
    </div>
  </div>
  <div
    class="receipt receipt-end border-0 shadow"
    tabindex="-1"
    style="visibility: visible"
    aria-modal="true"
    role="dialog"
    :class="{ 'show-receipt': selectedOrder != null }"
  >
    <div class="receipt-header bg-dark">
      <h4 class="receipt-title">RECEIPT</h4>
      <div class="text-center text-nowrap">
        <a class="btn btn-icon btn-xs btn-primary" href="javascript:void(0)"
          ><i class="demo-pli-download-from-cloud fs-4 fw-bolder"></i
        ></a>
        <a
          class="btn btn-icon btn-xs btn-success mx-2"
          href="javascript:void(0)"
          @click.prevent="handleReceiptPrint('print-container')"
        >
          <svg
            height="18px"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M17 17h2a2 2 0 002-2v-4a2 2 0 00-2-2H5a2 2 0 00-2 2v4a2 2 0 002 2h2m2 4h6a2 2 0 002-2v-4a2 2 0 00-2-2H9a2 2 0 00-2 2v4a2 2 0 002 2zm8-12V5a2 2 0 00-2-2H9a2 2 0 00-2 2v4h10z"
            ></path></svg
        ></a>
        <a
          class="btn btn-icon btn-xs btn-danger"
          href="javascript:void(0)"
          @click.prevent="selectOrder(null)"
          ><i class="demo-pli-cross fs-5 fw-bolder"></i
        ></a>
      </div>
    </div>
    <div class="receipt-body scrollable-content px-1">
      <receipt-one
        v-if="selectedOrder != null"
        :order="selectedOrder"
      ></receipt-one>
    </div>
  </div>
</template>

