<script>
import { Chart as ChartJS } from "chart.js";
import { backgrounds } from "./dashboard.js";
export default {
  name: "CategoriesChart",
  props: {
    categoriesChart: {
      type: Object,
      default: function () {
        return {};
      },
    },
    totalOrders: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    chartData() {
      return {
        labels: Object.keys(this.categoriesChart),
        dataUnit: "Orders",
        legend: !1,
        datasets: [
          {
            borderColor: "#fff",
            backgroundColor: [...backgrounds],
            data: Object.values(this.categoriesChart),
            borderWidth: 2,
            hoverBorderColor: "#fff",
          },
        ],
      };
    },
    keys() {
      return Object.keys(this.categoriesChart);
    },
  },
  updated() {
    this.$nextTick(() => {
      new ChartJS(document.querySelector("#categories-chart"), {
        type: "pie",
        data: this.chartData,
        options: {
          legend: {
            display: !1,
            rtl: false,
            labels: { boxWidth: 12, padding: 20, fontColor: "#6783b8" },
          },
          maintainAspectRatio: !1,
          tooltips: {
            enabled: !0,
            rtl: false,
            callbacks: {
              title: function (a, t) {
                return t.labels[a[0].index];
              },
              label: function (a, t) {
                return t.datasets[a.datasetIndex].data[a.index] + " Orders";
              },
            },
            backgroundColor: "#eff6ff",
            titleFontSize: 13,
            titleFontColor: "#6783b8",
            titleMarginBottom: 6,
            bodyFontColor: "#9eaecf",
            bodyFontSize: 12,
            bodySpacing: 4,
            yPadding: 10,
            xPadding: 10,
            footerMarginTop: 0,
            displayColors: !1,
          },
        },
      });
    });
  },
  methods: {
    getColor(item) {
      let index = this.keys.indexOf(item);
      return backgrounds[index];
    },
  },
};
</script>
<template>
  <div class="card">
    <div class="card-body">
      <div class="card-head">
        <h4 class="mb-0">Categories Chart</h4>
      </div>
      <div class="row">
        <div class="col-lg-8">
          <div class="nk-ck-sm">
            <canvas class="polar-chart" id="categories-chart"></canvas>
          </div>
        </div>
        <div class="col-lg-4">
          <div
            class="d-flex flex-nowrap align-items-center"
            v-for="(item, i) in categoriesChart"
            :key="item"
          >
            <span
              class="badge rounded p-1"
              :style="`background-color:${getColor(i)}`"
              ><span class="visually-hidden"></span
            ></span>
            <span class="ms-1">{{
              `${i}(${parseInt((item * 100) / totalOrders)}%)`
            }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
