<script>
import { menus } from "@/index/shared/menus.js";
import { menus as frontMenus } from "@/front-office/shared/menus.js";
export default {
  name: "UserRoleCreator",
  props: ["role"],
  emits: ["saved"],
  data: () => ({
    newRole: {},
    displayMode: true,
  }),
  computed: {
    user() {
      return this.$store.state.user || {};
    },
    isReady() {
      return this.user && Object.keys(this.user).length > 0;
    },
    priviledges() {
      return this.isReady && this.user.role.permissions;
    },
  },
  created() {
    let permissions = { back_office: {}, front_office: {} };
    menus.forEach((menu) => {
      permissions.back_office[menu.title] = {
        accessible: false,
      };
      if (menu.submenus) {
        for (let key in menu.submenus) {
          permissions.back_office[menu.title][key] = [];
        }
      } else {
        permissions.back_office[menu.title].access = [];
      }
    });
    frontMenus.forEach((menu) => {
      permissions.front_office[menu.title] = {
        accessible: false,
      };
      permissions.front_office[menu.title].access = [];
    });
    if (this.role) {
      this.$http.get(`users/roles/show/${this.role.id}`).then((response) => {
        this.newRole = { ...response.data.row };
        if (this.newRole.permissions) {
          this.newRole.permissions = {
            ...permissions,
            ...this.newRole.permissions,
          };
        } else {
          this.newRole.permissions = permissions;
        }
      });
    } else {
      this.newRole.permissions = permissions;
    }
  },
  methods: {
    toggleCrudMode() {
      this.displayMode = !this.displayMode;
    },
    canAccess(menu, submenu = null) {
      if (!this.user.role_id) {
        return true;
      } else {
        if (!submenu) {
          return this.priviledges[menu].accessible == true;
        } else {
          return this.priviledges[menu][submenu].includes("R");
        }
      }
    },
    grantFullAccess(e, item, menu, group = "back_office") {
      if (e.target.checked) {
        this.newRole.permissions[group][menu][item] = ["C", "R", "U", "D"];
      } else {
        this.newRole.permissions[group][menu][item] = [];
      }
    },
    handleSave() {
      this.$http
        .post("users/roles/store", this.$helper.generateFormData(this.newRole))
        .then((response) => {
          if (response.data.status) {
            this.$notify({
              type: "success",
              message: "Record saved successfully",
            });
            this.$emit("saved", response.data.row);
          }
        });
    },
  },
};
</script>
