<script>
import { permissionsHandler } from "@/common/permissions-handler";
export default {
  name: "OrderModal",
  props: ["order"],
  emits: ["closed", "paid"],
  mixins: [permissionsHandler],
  data: () => ({
    payment_method: null,
    account_id: null,
    payment_ref: null,
    comment: null,
  }),
  computed: {
    submitDisabled() {
      return !this.payment_method || !this.account_id;
    },
    accounts() {
      return this.$store.state.accounts;
    },
    paymentModes() {
      return this.$store.state.paymentModes;
    },
  },
  created() {},
  methods: {
    togglePaymentWrapper() {
      this.$el
        .querySelector(".payment-wrapper")
        .classList.toggle("payment-hidden");
    },
    handleDestroy(orderId) {
      this.$http.get(`orders/delete/${orderId}`).then((response) => {
        if (response.data.status) {
          let index = this.rows.findIndex((item) => item.id == orderId);
          this.rows.splice(index, 1);
          this.closeSidebar();
          this.$store.commit("SET_FLASH_MESSAGE", {
            type: "success",
            message: "Order deleted successfully",
          });
        }
      });
    },
    handlePayment() {
      this.$http
        .post(
          "orders/payment",
          this.$helper.generateFormData({
            order: this.order.id,
            payment_method: this.payment_method,
            account_id: this.account_id,
            payment_ref: this.payment_ref,
            comment: this.comment,
            discount: 0,
          })
        )
        .then((response) => {
          if (response.data.status) {
            this.$emit("paid");
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: `Payment to order ${this.order.reference} has been made sucessfully`,
            });
          }
        });
    },
  },
};
</script>
<template>
