    <div class="sidebar__inner pt-2 px-2 overflow-hidden">
      <div
        class="sidebar__stuck align-item-center d-flex py-2 border-bottom mb-2"
      >
        <h5 class="fw-bolder mb-0">
          Order No#{{ $helper.generateVoucherNo(order.id) }}
        </h5>
        <div class="ms-auto">
          <a
            href="javascript:void(0)"
            class="text-primary ms-1 border rounded fw-bolder"
            @click.prevent="$emit('closed')"
            style="padding: 0.1rem 0.25rem"
            ><i class="demo-pli-cross fw-bold icon fs-6"></i
          ></a>
        </div>
      </div>

      <div class="sidebar__wrap pos-cart scrollable-content">
        <div class="rounded shadown bg-white p-2 mb-3">
          <h6 class="m-0 text-uppercase">EXTERNAL NOTES</h6>
          <p class="mb-0">Extras Suggar</p>
        </div>

        <div class="">
          <div class="accordion mb-3" id="_dm-customerAccordion">
            <div class="accordion-item">
              <div class="accordion-header" id="_dm-defAccHeadingOne">
                <button
                  class="accordion-button"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#_dm-defAccCollapseOne"
                  aria-expanded="true"
                  aria-controls="_dm-defAccCollapseOne"
                >
                  <div class="d-flex align-items-center">
                    <span class="bg-dark text-white p-1-1 rounded-circle me-1">
                      <i class="demo-psi-male"></i>
                    </span>
                    <span class="me-3 fw-bold">Customers</span>
                    <span class="qty-circle text-white rounded-circle p-1-1">
                      1
                    </span>
                  </div>
                </button>
              </div>
              <div
                id="_dm-defAccCollapseOne"
                class="accordion-collapse collapse show"
                aria-labelledby="_dm-defAccHeadingOne"
                data-bs-parent="#_dm-customerAccordion"
                style=""
              >
                <div class="accordion-body p-2">
                  <div class="bg-white rounded shadow px-3 py-3 mb-2">
                    {{ order.client ? order.name : "Walk-in Customer" }}
                  </div>
                  <a href="javascript:void(0)" class="">
                    <span></span>
                    Add Customer
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div class="accordion" id="_dm-summaryAccordion">
            <div class="accordion-item">
              <div class="accordion-header" id="_dm-defAccHeadingTwo">
                <button
                  class="accordion-button"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#_dm-defAccCollapseTwo"
                  aria-expanded="true"
                  aria-controls="_dm-defAccCollapseTwo"
                >
                  <div class="d-flex align-items-center w-100">
                    <span
                      class="bg-gray-900 text-white p-1 rounded-circle me-1"
                    >
                      <svg
                        height="15px"
                        viewBox="0 0 16 16"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="currentColor"
                      >
                        <path
                          fill-rule="evenodd"
                          d="M0 2.5A.5.5 0 0 1 .5 2H2a.5.5 0 0 1 .485.379L2.89 4H14.5a.5.5 0 0 1 .485.621l-1.5 6A.5.5 0 0 1 13 11H4a.5.5 0 0 1-.485-.379L1.61 3H.5a.5.5 0 0 1-.5-.5zM3.14 5l.5 2H5V5H3.14zM6 5v2h2V5H6zm3 0v2h2V5H9zm3 0v2h1.36l.5-2H12zm1.11 3H12v2h.61l.5-2zM11 8H9v2h2V8zM8 8H6v2h2V8zM5 8H3.89l.5 2H5V8zm0 5a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm-2 1a2 2 0 1 1 4 0 2 2 0 0 1-4 0zm9-1a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm-2 1a2 2 0 1 1 4 0 2 2 0 0 1-4 0z"
                        ></path>
                      </svg>
                    </span>
                    <span class="me-3">Order Summary</span>
                    <span class="ms-auto fw-bolder m-2 h5 mb-0">{{
                      formatMoney(order.grand_total)
                    }}</span>
                  </div>
