            ></a>
          </div>
        </div>
        <div class="payment-form py-3">
          <div class="row mb-2">
            <label class="col-lg-3 col-md-5 required">Payment Method</label>
            <div class="col-lg-6 col-md-7">
              <select class="form-control form-select" v-model="payment_method">
                <option value="null" hidden disabled>Select option</option>
                <option
                  :value="row.id"
                  :key="row.id"
                  v-for="row in paymentModes"
                >
                  {{ row.name }}
                </option>
              </select>
            </div>
          </div>
          <div class="row mb-2">
            <label class="col-lg-3 col-md-5 required">Deposited To</label>
            <div class="col-lg-6 col-md-7">
              <select class="form-control form-select" v-model="account_id">
                <option value="null" hidden disabled>Select option</option>
                <option :value="row.id" :key="row.id" v-for="row in accounts">
                  {{ row.name }}
                </option>
              </select>
            </div>
          </div>
          <div class="row mb-2">
            <label class="col-lg-3 col-md-5">Payment Ref</label>
            <div class="col-lg-6 col-md-7">
              <input type="text" v-model="payment_ref" class="form-control" />
            </div>
          </div>
          <div class="row mb-2">
            <label class="col-lg-3 col-md-5">Comment</label>
