<script>
import Avatar from "@/components/Avatar.vue";
import OffCanvas from "@/components/OffCanvas.vue";
export default {
  name: "Users",
  components: {
    Avatar,
    OffCanvas,
  },
  data: () => ({
    rows: [],
    roles: [],
    newUser: {
      first_name: null,
      last_name: null,
      email: null,
      user_pin: null,
      address: null,
      phone: null,
      gender: null,
      branch_id: null,
      role_id: null,
      avatar: null,
      username: null,
    },
    credentials: {
      password: null,
      new_password: null,
      id: null,
    },
    passwordModal: false,
    branches: [],
    passcodeExists: false,
  }),
  created() {
    this.$http.get("users").then((response) => {
      this.rows = response.data.users;
    });
  },
  mounted() {
    this.$http.get("users/roles").then((response) => {
      if (response.data.status) {
        this.roles = response.data.roles;
      }
    });
    this.$http.get("branches").then((response) => {
      this.branches = response.data.branches;
    });
  },
  methods: {
    handleSubmit() {
      this.passcodeExists = false;
      this.$http
        .post("users/store", this.$helper.generateFormData(this.newUser))
        .then((response) => {
          if (response.data.status) {
            this.rows.unshift(response.data.user);
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "User Added sucessfully",
            });
            this.toggleModal();
          }
        })
        .catch((err) => {
          if (err.response.status == 422) {
            this.passcodeExists = true;
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "danger",
              message: "User with the same PIN already exists",
            });
          }
        });
    },
  },
};
</script>
<template>
  <off-canvas @close="toggleModal" position="end" v-if="modalOpen">
    <template #head>
      {{ newUser.id ? "Edit User information" : "Create a New Account" }}
    </template>
    <template #body>
      <form class="forms-sample">
        <div class="row">
          <div class="col-md-6">
            <div class="form-group">
              <label class="form-label">First Name</label>
              <input
                type="text"
                v-model="newUser.first_name"
                name="First Name"
                class="form-control"
                placeholder="First Name"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group mb-3">
              <label class="form-label">Last Name</label>
              <input
                type="text"
                v-model="newUser.last_name"
                name="Last Name"
                class="form-control"
                placeholder="Last Name"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group">
              <label class="form-label">Gender</label>
              <br />
              <div class="col-sm-8 col-xl-10 mb-3">
                <div class="form-check form-check-inline">
                  <input
                    id="_dm-wRegMemberType1"
                    class="form-check-input"
                    type="radio"
                    name="member-type"
                    value="M"
                    v-model="newUser.gender"
                  />
                  <label for="_dm-wRegMemberType1" class="form-check-label"
                    >Male</label
                  >
                </div>
                <div class="form-check form-check-inline">
                  <input
                    id="_dm-wRegMemberType2"
                    class="form-check-input"
                    type="radio"
                    name="member-type"
                    value="F"
                    v-model="newUser.gender"
                  />
                  <label for="_dm-wRegMemberType2" class="form-check-label"
                    >Female</label
                  >
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group mb-3">
              <label class="form-label">Phone Number</label>
              <input
                type="text"
                v-model="newUser.phone"
                name="Phone"
                class="form-control"
                placeholder="Phone Number"
              />
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div class="form-group mb-3">
              <label class="form-label">Email</label>
              <input
                type="text"
                v-model="newUser.email"
                name="Email"
                class="form-control"
                placeholder="Email Address"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group mb-3">
              <label class="form-label">Address</label>
              <input
                type="text"
                v-model="newUser.address"
                class="form-control"
                placeholder="Address Line"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group mb-3">
              <label class="form-label">Branch</label>
              <div class="form-control-wrap">
                <div class="form-control-select">
                  <select
                    class="form-control form-select"
                    id="default-06"
                    v-model="newUser.branch_id"
                    name="Branch"
                  >
                    <option value="null" hidden disabled>Select Branch</option>
                    <option
                      :value="item.id"
                      v-for="(item, i) in branches"
                      :key="'branch' + i"
                    >
                      {{ item.name }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group mb-3">
              <label class="form-label">Role</label>
              <div class="form-control-wrap">
                <div class="form-control-select">
                  <select
                    class="form-control form-select"
                    id="default-06"
                    v-model="newUser.role_id"
                    name="Role"
                  >
                    <option value="null" hidden disabled>Select Role</option>
                    <option
                      :value="item.id"
                      v-for="(item, i) in roles"
                      :key="'role' + i"
                    >
                      {{ item.name }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-6" v-if="$helper.empty(user)">
            <div class="form-group mb-3">
              <label class="form-label">Password</label>
              <input
                type="Password"
                v-model="newUser.password"
                name="Password"
                class="form-control"
                placeholder="Password"
              />
            </div>
          </div>
          <div class="col-md-6" v-if="$helper.empty(user)">
            <div class="form-group mb-3">
              <label class="form-label">Passcode</label>
              <input
                type="password"
                v-model="newUser.user_pin"
                class="form-control"
                placeholder="6 digitis PIN"
                :class="{ 'is-invalid': passcodeExists }"
                @input="passcodeExists = false"
              />
              <div class="invalid-feedback" v-if="passcodeExists">
                Please choose a different passcode.
              </div>
            </div>
          </div>
        </div>

        <div class="my-3">
          <wolf-button
            class="btn btn-primary"
            @clicked="handleSubmit"
            :disabler="
              !newUser.first_name || (!newUser.password && !newUser.user_pin)
            "
            activator="saving_records"
            >Save and Close</wolf-button
          >
        </div>
      </form>
    </template>
  </off-canvas>
  <div class="content__header content__boxed overlapping">
    <div class="content__wrap">
      <div class="d-md-flex align-items-end">
        <div class="me-auto">
          <h1 class="page-title mb-0 mt-2">Users</h1>

          <p class="lead mb-lg-0">List of all users including waiters</p>
        </div>
        <div
          class="flex-grow-1 d-flex flex-wrap justify-content-end align-items-center gap-3"
        >
          <button
            type="button"
            class="btn btn-secondary btn-lg hstack gap-2"
            @click="toggleModal"
          >
            <i class="demo-psi-add fs-4"></i>
            <span class="vr"></span>
            Add new
          </button>

          <div class="w-auto text-nowrap">
            <span class="d-inline-block me-2">Filter by : </span>
            <select class="d-inline-block w-auto form-select">
              <option value="date-created" selected="">Waiters</option>
              <option value="date-modified">Cashiers</option>
            </select>
            <button type="button" class="btn btn-light ms-2">Filter</button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="row mt-3">
        <div
          class="col-sm-6 col-md-4 col-xl-3 mb-3"
          v-for="(row, i) in rows"
          :key="row.name + i"
        >
          <div class="card">
            <div class="card-body pt-2">
              <div class="d-flex justify-content-end gap-1">
                <div class="dropdown">
                  <button
                    class="btn btn-sm btn-icon btn-hover btn-light shadow-none"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <i class="demo-pli-dot-horizontal fs-4"></i>
                    <span class="visually-hidden">Toggle Dropdown</span>
                  </button>
                  <ul class="dropdown-menu dropdown-menu-end">
                    <li>
                      <a href="javascript:void(0)" class="dropdown-item">
                        <i class="demo-pli-pen-5 fs-5 me-2"></i> Edit
                      </a>
                    </li>
                    <li>
                      <a
                        href="javascript:void(0)"
                        class="dropdown-item text-danger"
                      >
                        <i class="demo-pli-recycling fs-5 me-2"></i> Remove
                      </a>
                    </li>
                    <li>
                      <a href="javascript:void(0)" class="dropdown-item">
                        <i class="demo-pli-calendar-4 fs-5 me-2"></i> View
                        Details
                      </a>
                    </li>
                    <li>
                      <a href="javascript:void(0)" class="dropdown-item">
                        <i class="demo-pli-lock-user fs-5 me-2"></i> Lock
                      </a>
                    </li>
                  </ul>
                </div>
              </div>

              <div class="text-center position-relative">
                <div class="pb-3 d-flex justify-content-center">
                  <avatar
                    :name="row.name"
                    width="4.25rem"
                    height="4.25rem"
                    :is-large="true"
                  ></avatar>
                </div>
                <a
                  href="javascript:void(0)"
                  class="h5 stretched-link btn-link"
                  >{{ row.name }}</a
                >
                <p class="mb-0">
                  @{{ row.name.toLowerCase().split(" ").join("_") }}
                </p>
                <a href="javascript:void(0)" class="text-primary mb-3">
                  {{ row.role.name }}
                </a>
                <p class="mb-1">
                  <i class="demo-psi-male fs-5"></i>
                  {{ row.email || row.phone }}
                </p>
                <p>
                  <i class="demo-psi-office fs-5"></i>
                  {{ row.branch ? row.branch.name : "HeadQuarter" }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
