<script>
import Avatar from "@/components/Avatar.vue";
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import ClientCreator from "./ClientCreator.vue";
export default {
  name: "Clients",
  mixins: [dataTableService],
  components: {
    Avatar,
    ContentHeader,
    ClientCreator,
  },
  data: () => ({
    fetchUrl: "clients",
    client: null,
  }),
  methods: {
    handleAddedClient(client) {
      if (this.client) {
        let index = this.rows.findIndex((row) => row.id == this.client.id);
        this.rows[index] = client;
      } else {
        this.rows.shift(client);
      }
      this.client = null;
      this.toggleModal();
    },
    handleDestroy(id, index) {
      this.$http.get(`clients/delete/${id}`).then(() => {
        this.rows.splice(index, 1);
        this.$store.commit("SET_FLASH_MESSAGE", {
          type: "success",
          message: "Client Deleted successfully",
        });
      });
    },
    handleEdit(client) {
      this.client = client;
      this.toggleModal();
    },
  },
};
</script>
<template>
  <content-header
    title="Clients"
    subtitle="List of all registered clients"
  ></content-header>
  <client-creator
    v-if="modalOpen"
    @closed="toggleModal"
    @saved="handleAddedClient"
    :client="client"
  />
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div class="col-md-6 d-flex gap-1 align-items-center mb-3">
              <button
                class="btn btn-primary hstack gap-2 align-self-center"
                @click="toggleModal"
              >
                <i class="demo-psi-add fs-5"></i>
                <span class="vr"></span>
                Add New
              </button>
              <button class="btn btn-icon btn-outline-light">
                <i class="demo-pli-printer fs-5"></i>
              </button>
            </div>
            <div
              class="col-md-6 d-flex gap-1 align-items-center justify-content-md-end mb-3"
            >
              <div class="form-group">
                <input
                  type="text"
                  placeholder="Search..."
                  class="form-control"
                  autocomplete="off"
                />
              </div>
              <div class="btn-group">
                <button class="btn btn-icon btn-outline-light">
                  <i class="demo-pli-download-from-cloud fs-5"></i>
                </button>
                <div class="btn-group dropdown">
                  <button
                    class="btn btn-icon btn-outline-light dropdown-toggle dropdown-toggle-split"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <span class="visually-hidden">Toggle Dropdown</span>
                  </button>
                  <ul class="dropdown-menu dropdown-menu-end" style="">
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >All Clients</a
                      >
                    </li>
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >Individuals</a
                      >
                    </li>
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >Companies</a
                      >
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card-body">
          <div class="table-responsive">
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th class="text-center">$/N</th>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Phone</th>
                  <th>Email</th>
                  <th>Tin</th>
                  <th>Address</th>
                  <th>Discount</th>
                  <th>Unpaid Amount</th>
                  <th>Created By</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(client, i) in rows" :key="client.id">
                  <td class="text-center">#{{ i + 1 }}</td>
                  <td>
                    <a
                      href="javascript:void(0)"
                      class="d-flex align-items-center"
                    >
                      <avatar
                        :name="client.name"
                        width="2rem"
                        height="2rem"
                      ></avatar>
                      <span class="fw-bold ms-2">{{ client.name }}</span>
                    </a>
                  </td>
                  <td>
                    {{ client.type }}
                  </td>
                  <td>
                    {{ client.phone || "N/A" }}
                  </td>
                  <td>
                    {{ client.email || "N/A" }}
                  </td>
                  <td>
                    {{ client.tin_number }}
                  </td>
                  <td>
                    {{ client.address }}
                  </td>
                  <td>{{ client.discount || 0 }}%</td>
                  <td>
                    <span class="fw-bold">{{ formatMoney(0) }}</span>
                  </td>
                  <td>
                    <a href="javascript:void(0)" class="btn-link">
                      {{ client.created_by || "Admin" }}
                    </a>
                  </td>
                  <td class="text-center text-nowrap">
                    <div class="dropdown">
                      <a
                        href="#"
                        class="dropdown-toggle btn btn-icon btn-trigger"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      >
                        <i class="icon demo-pli-dot-horizontal"></i>
                      </a>
                      <div class="dropdown-menu dropdown-menu-end">
                        <ul class="link-list-opt no-bdr" style="">
                          <li>
                            <a
                              href="javascript:void(0)"
                              @click.prevent="handleEdit(client)"
                              ><i class="demo-pli-pen-5 icon"></i>
                              <span>Edit</span>
                            </a>
                          </li>
                          <li>
                            <a
                              href="javascript:void(0)"
                              title="Delete Client"
                              @click.prevent="
                                $confirm({
                                  title: 'Delete Client',
                                  content: `Are you sure you want to delete ${client.name} ?`,
                                  classes: 'btn btn-danger',
                                  actionText: 'Yes',
                                  closeText: 'Cancel',
                                  action: () => {
                                    handleDestroy(client.id, i);
                                  },
                                })
                              "
                              ><i class="demo-pli-trash icon"></i>
                              <span>Delete</span>
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <pagination
            :from="recordsFrom"
            :to="recordsTo"
            :current_page="currentPage"
            :row_count_page="rowCountPage"
            @page-update="handlePagination"
            :total_records="totalRows"
            :page_range="pageRange"
          ></pagination>
        </div>
      </div>
    </div>
  </div>
</template>
