<template>
  <div
    class="content__boxed w-100 min-vh-100 d-flex flex-column align-items-center justify-content-center"
  >
    <div class="content__wrap">
      <div class="card shadow-lg">
        <div class="card-body">
          <div class="text-center">
            <img src="/img/logo.png" class="w-50 mb-4" alt="" />
            <h1 class="h3">POS Account Login</h1>
            <p>Sign In to your tame pos account</p>
          </div>
          <form class="mt-4" @submit.prevent="authenticateUser">
            <div class="mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="Username"
                v-model="credentials.email"
                autofocus
              />
            </div>

            <div class="mb-3">
              <input
                type="password"
                class="form-control"
                placeholder="Password"
                v-model="credentials.password"
                name="password"
              />
            </div>
            <div class="d-grid mt-2">
              <wolf-button
                type="submit"
                @clicked="authenticateUser"
                :disabler="!credentials.email || !credentials.password"
                activator="SIGNING_REQUEST"
                class="btn btn-primary btn-lg"
                >Sign In</wolf-button
              >
            </div>
          </form>

          <div class="d-flex justify-content-between mt-3" v-if="false">
            <router-link
              :to="{ name: '' }"
              class="btn-link text-decoration-none"
              >Forgot password ?</router-link
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//import { Form, Field, ErrorMessage } from 'vee-validate';
export default {
  name: "login",
  data: () => ({
    credentials: {
      email: null,
      password: null,
    },
    invalidLogin: false,
    loginSucceed: false,
    passwordType: "password",
  }),
  created() {
    if (this.$store.getters.isLoggedIn) {
      if (navigator.userAgent.indexOf("Firefox") > -1)
        setTimeout(() => {
          location.replace("/dashboard");
        }, 2000);
      else location.replace("/dashboard");
    }
  },
  methods: {
    handleVisibility() {
      if (this.passwordType == "password") this.passwordType = "text";
      else this.passwordType = "password";
    },
    authenticateUser() {
      this.$http
        .post("auth/login", this.$helper.generateFormData(this.credentials))
        .then((response) => {
          if (response.data.status) {
            const token = response.data.access_token || response.data.token;
            if (token) {
              localStorage.setItem("token", token);
            }
            /*this.invalidLogin = false;
            this.loginSucceed = true; */
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "Login Succeed. Redirecting...",
            });
            if (navigator.userAgent.indexOf("Firefox") > 0)
              setTimeout(() => {
                location.replace("/dashboard");
              }, 3000);
            else location.replace("/dashboard");
          }
        })
        .catch((err) => {
          if (err.response.status == 401) {
            /*this.invalidLogin = true;
            this.loginSucceed = false; */
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "danger",
              message: "Invalid Password Or Username. Try Again",
            });
          }
        });
    },
  },
};
</script>
