<script>
import { bars } from "./dashboard";
import { Chart as ChartJS } from "chart.js";
export default {
  name: "OrdersChart",
  props: {
    ordersChart: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  computed: {
    chartData() {
      return {
        labels: this.ordersChart.labels,
        dataUnit: "Orders",
        datasets: [
          {
            label: "Orders",
            color: "#e85347",
            backgroundColor: "#25476a",
            data: this.ordersChart.data,
          },
        ],
      };
    },
  },
  updated() {
    this.$nextTick(() => {
      new ChartJS(document.querySelector("#orders-chart"), {
        type: "bar",
        data: this.chartData,
        options: bars,
      });
    });
  },
};
</script>
<template>
  <div class="card">
    <div class="card-body">
      <div class="card-head"><h4 class="title">Orders Chart</h4></div>
      <div class="nk-ck">
        <canvas class="bar-chart" id="orders-chart"></canvas>
      </div>
    </div>
  </div>
</template>
