<template>
  <content-header
    title="All Tables"
    subtitle="Tables list and their associative branches"
  ></content-header>
  <bootstrap-modal v-if="modalOpen" @close="toggleModal">
    <template #head>
      <span v-if="!this.newTable.id">Add a new table</span>
      <span v-else>Edit Table Number #{{ newTable.id }}</span>
    </template>
    <template #body>
      <h5 class="card-title">Fill Table Information</h5>
      <form>
        <div class="row mb-3">
          <label for="table-name" class="col-sm-3 col-form-label"
            >Table Name</label
          >
          <div class="col-sm-9">
            <input
              type="text"
              v-model="newTable.name"
              class="form-control"
              id="table-name"
            />
          </div>
        </div>

        <div class="row mb-3">
          <label for="capacity" class="col-sm-3 col-form-label">Capacity</label>
          <div class="col-sm-9">
            <input
              type="number"
              v-model="newTable.capacity"
              class="form-control"
              id="capacity"
            />
          </div>
        </div>

        <div class="row mb-3">
          <label for="branch" class="col-sm-3 form-label">Branch</label>
          <div class="col-sm-9">
            <select
              id="branch"
              type="text"
              class="form-select"
              v-model="newTable.branch_id"
            >
              <option value="null" hidden disabled>Select Branch</option>
              <option
                :value="branch.id"
                v-for="branch in branches"
                :key="branch.id"
              >
                {{ branch.name }}
              </option>
            </select>
          </div>
        </div>

        <div class="row mb-3">
          <label for="description" class="col-sm-3 col-form-label"
            >Description</label
          >
          <div class="col-sm-9">
            <textarea
              id="description"
              v-model="newTable.description"
              class="form-control"
              placeholder="Optional Comment"
            ></textarea>
          </div>
        </div>

        <div class="row mb-3">
          <div class="col-sm-9 offset-sm-3">
            <div class="form-check">
              <input
                id="table-status"
                class="form-check-input"
                type="checkbox"
                v-model="newTable.active"
              />
              <label for="table-status" class="form-check-label">
                Table is Active
              </label>
            </div>
          </div>
        </div>
        <zoho-button
          type="button"
          class="btn btn-primary"
          @clicked="handleSubmit"
          :disabler="!newTable.name || !newTable.capacity"
          activator="MODAL_REQUEST"
          >Save</zoho-button
        >
      </form>
    </template>
  </bootstrap-modal>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div class="col-md-6 d-flex gap-1 align-items-center mb-3">
              <button
                class="btn btn-primary hstack gap-2 align-self-center"
                @click="toggleModal"
              >
                <i class="demo-psi-add fs-5"></i>
                <span class="vr"></span>
                Add New
              </button>
              <button class="btn btn-icon btn-outline-light">
                <i class="demo-pli-printer fs-5"></i>
              </button>
            </div>
            <div
              class="col-md-6 d-flex gap-1 align-items-center justify-content-md-end mb-3"
            >
              <div class="form-group">
                <input
                  type="text"
                  placeholder="Search..."
                  class="form-control"
                  autocomplete="off"
                />
              </div>
              <div class="btn-group">
                <button class="btn btn-icon btn-outline-light">
                  <i class="demo-pli-download-from-cloud fs-5"></i>
                </button>
                <div class="btn-group dropdown">
                  <button
                    class="btn btn-icon btn-outline-light dropdown-toggle dropdown-toggle-split"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <span class="visually-hidden">Toggle Dropdown</span>
                  </button>
                  <ul class="dropdown-menu dropdown-menu-end" style="">
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >All Tables</a
                      >
                    </li>
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >Active</a
                      >
                    </li>
                    <li>
                      <a class="dropdown-item" href="javascript:void(0)"
                        >Inactive</a
                      >
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <!-- END : Right Toolbar -->
          </div>
        </div>
        <div class="card-body">
          <div class="table-responsive">
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th class="text-center">$/N</th>
                  <th>Table Name</th>
                  <th>Capacity</th>
                  <th>Branch</th>
                  <th>Status</th>
                  <th>Description</th>
                  <th>Created at</th>
                  <th>Created By</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, i) in rows" :key="item.id">
                  <td class="text-center">#{{ i + 1 }}</td>
                  <td>
                    <a href="javascript:void(0)" class="btn-link">{{
                      item.name || `Table #${item.id}`
                    }}</a>
                  </td>
                  <td>
                    {{ item.capacity }}
                  </td>
                  <td>
                    <a href="javascript:void(0)" class="btn-link">{{
                      item.branch ? item.branch.name : "Main Branch"
                    }}</a>
                  </td>
                  <td>
                    <span class="text-success" v-if="item.active">Active</span>
                    <span class="text-danger" v-else>Inactive</span>
                  </td>
                  <td>
                    {{ item.description || "N/A" }}
                  </td>
                  <td>
                    {{ this.$helper.formatDate(item.created_at) }}
                  </td>
                  <td>
                    <a href="javascript:void(0)" class="btn-link">{{
                      item.creator ? item.creator.name : "Administrator"
                    }}</a>
                  </td>
                  <td class="text-center text-nowrap">
                    <div class="dropdown">
                      <a
                        href="#"
                        class="dropdown-toggle btn btn-icon btn-trigger"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      >
                        <i class="icon demo-pli-dot-horizontal"></i>
                      </a>
                      <div class="dropdown-menu dropdown-menu-end">
                        <ul class="link-list-opt no-bdr" style="">
                          <li>
                            <a href="javascript:void(0)"
                              ><i class="demo-pli-pen-5 icon"></i>
                              <span>Edit</span>
                            </a>
                          </li>
                          <li>
                            <a
                              href="javascript:void(0)"
                              title="Delete Item"
                              @click.prevent="
                                $confirm({
                                  title: 'Delete Item from orders',
                                  content: `Are you sure you want to remove ${item.name} ?`,
                                  classes: 'btn btn-danger',
                                  actionText: 'Continue',
                                  closeText: 'Discard',
                                  action: () => {
                                    handleDestroy(item.id, i);
                                  },
                                })
                              "
                              ><i class="demo-pli-trash icon"></i>
                              <span>Delete</span>
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <pagination
            :from="recordsFrom"
            :to="recordsTo"
            :current_page="currentPage"
            :row_count_page="rowCountPage"
            @page-update="handlePagination"
            :total_records="totalRows"
            :page_range="pageRange"
          ></pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import BootstrapModal from "@/components/BootstrapModal.vue";
import ZohoButton from "@/components/ZohoButton.vue";
export default {
  name: "Index",
  mixins: [dataTableService],
  components: {
    ContentHeader,
    BootstrapModal,
    ZohoButton,
  },
  data: () => ({
    fetchUrl: "tables",
    newTable: {
      name: null,
      capacity: 1,
      description: null,
      branch_id: null,
      active: true,
    },
    branches: [],
  }),
  mounted() {
    this.$http.get("branches").then((response) => {
      this.branches = response.data.branches;
    });
  },
  methods: {
    handleSubmit() {
      this.$http
        .post("tables/store", this.$helper.generateFormData(this.newTable))
        .then((response) => {
          this.rows.unshift(response.data.table);
          this.newTable = {
            name: null,
            capacity: 1,
            description: null,
            branch_id: null,
            active: 1,
          };
          this.toggleModal();
          this.$store.commit("SET_FLASH_MESSAGE", {
            type: "success",
            message: "Record saved successfully",
          });
        });
    },
    handleDestroy(id, index) {
      this.$http.get(`tables/delete/${id}`).then(() => {
        this.rows.splice(index, 1);
        this.$store.commit("SET_FLASH_MESSAGE", {
          type: "success",
          message: "Table Deleted successfully",
        });
      });
    },
  },
};
</script>
