          ></pagination>
        </div>
      </div>
    </div>
  </div>
  <bootstrap-modal
    v-if="modalOpen"
    @close="toggleModal"
    style="display: block"
    :modalSize="'modal-lg'"
    :showFooter="false"
  >
    <template #head>
      <span style="padding-top: 10px">
        Item configurations <br />
        <small
          ><span class="text-primary text-capitalize">{{
            selectedProduct.name
          }}</span></small
        >
        &nbsp;
        <small
          ><i>Sales Price:</i>&nbsp;<span
            class="text-primary text-capitalize"
            >{{
              `${appSettings.currency} ${$helper.formatNumber(
                selectedProduct.price
              )}` || "N/A"
            }}</span
          ></small
        >
      </span>
    </template>

    <template #body>
      <div class="row">
        <div
          class="col-md-12 d-flex gap-1 align-items-center justify-content-md-end mb-3"
        >
          <ul class="nk-block-tools d-flex align-items-center g-1 w-100">
            <li>
              <zoho-search
                  url="items/ingredients"
                  label="name"
                  placeholder="Search for a ingredent"
                  @selected="selectedIngredient"
                  :load-defaults="true"
                ></zoho-search>
            </li>
            <li style="width: 120px">
              <input
                type="number"
                v-model="newItem.quantity"
                class="form-control"
                placeholder="Quantity"
              />
            </li>
            <li class="me-auto">
              <button
                type="button"
                class="btn btn-icon btn-primary"
                @click.prevent="addIngredient"
                :disabled="!newItem.quantity || $helper.empty(newItem.product)"
              >
                <svg
                  height="18px"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                >
                  <line x1="12" y1="5" x2="12" y2="19"></line>
                  <line x1="5" y1="12" x2="19" y2="12"></line>
                </svg>
              </button>
            </li>
          </ul>
        </div>
      </div>
      <div class="table-responsive">
        <table class="table">
          <thead>
            <tr>
              <th><span>#</span></th>
              <th><span>Ingredient</span></th>
              <th><span>Cost</span></th>
              <th><span>Quantity</span></th>
              <th><span>Amount</span></th>
              <th></th>
            </tr>
          </thead>
        </table>
        <div class="card card-full">
          <div class="nk-tb-list mt-n2">
            <div class="nk-tb-item nk-tb-head"></div>
            <div
              class="nk-tb-item"
              v-for="(item, i) in configurations"
              :key="'item' + i"
            >
              <div class="nk-tb-col">
                <span class="tb-lead"
                  ><a href="javascript:void(0)">#{{ i + 1 }}</a></span
                >
              </div>
              <div class="nk-tb-col">
                <div class="user-card">
                  <div class="user-name">
                    <span class="tb-lead">{{ item.name }}</span>
                  </div>
                </div>
              </div>
              <div class="nk-tb-col">
                <span class="tb-sub tb-amount"
                  ><span>{{ appSettings.currency }} </span
                  >{{ itemCost(item) }}</span
                >
              </div>
              <div class="nk-tb-col">
                <span>{{ item.quantity }}</span>
              </div>
              <div class="nk-tb-col">
                <span class="tb-sub tb-amount"
                  ><span>{{ appSettings.currency }} </span
                  >{{ itemTotalAmount(item) }}</span
                >
              </div>
              <div class="nk-tb-col nk-tb-col-tools">
                <a
                  href="javascript:void(0)"
                  class="text-danger"
                  @click.prevent="handleIngredientDeletion(item, i)"
                  ><em class="icon ni ni-trash text-danger"></em><span></span
                ></a>
              </div>
            </div>
          </div>
          <template v-if="configurations.length">
            <hr class="preview-hr my-2" />
            <div class="w-100 d-flex align-items-center">
              <h5 class="mb-0">Total Amount</h5>
              <h6 class="mb-0 ml-auto">
                {{ appSettings.currency }}
                {{ $helper.formatNumber(totalAmount) }}
                <em>({{ percentage.toFixed(2) }}%)</em>
              </h6>
            </div>
            <hr class="preview-hr my-2" />
            <div class="text-right w-100">
              <wolf-button
                class="btn btn-primary ml-2"
                @clicked="handleSubmit"
                :disabler="hasNoChanges"
                activator="saving_records"
              >
                Save Changes
              </wolf-button>
            </div>
          </template>
        </div>
      </div>
    </template>
  </bootstrap-modal>
</template>
<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import BootstrapModal from "@/components/BootstrapModal.vue";
import ZohoSearch from "@/components/ZohoSearch.vue";
export default {
  name: "Index",
  mixins: [dataTableService],
  components: {
    ContentHeader,
    BootstrapModal,
    ZohoSearch,
  },
  data: () => ({
    fetchUrl: "items",
    configurations: [],
    newItem: {
      product: null,
      quantity: 1,
    },
    ingredients: [],
    selectedItemIds: [],
    currentIngredients: [],
    selectedProduct: null,
    configuredIndex: null,
  }),
  computed: {
    totalAmount() {
      return this.configurations.reduce(
        (prev, cur) =>
          cur.tariff != null
            ? prev + cur.quantity * cur.tariff
            : prev + Number(cur.total_amount),
        0
      );
    },
    hasNoChanges() {
      return this.configurations.length == this.currentIngredients.length;
    },
    percentage() {
      let salesPrice = this.selectedProduct.price || 0;
      if (!salesPrice) return 0;
      return (this.totalAmount * 100) / salesPrice;
    },
  },
  methods: {
    itemCost(item) {
      if (item.tariff) return this.$helper.formatNumber(item.tariff);
      else return this.$helper.formatNumber(item.cost);
    },
    itemTotalAmount(item) {
      if (item.tariff)
        return this.$helper.formatNumber(item.quantity * item.tariff);
      else return this.$helper.formatNumber(item.total_amount);
    },
    calculateItemPercentage(item) {
      let costPrice = item.cost_price || 0;
      let salesPrice = item.price || 0;
      if (!salesPrice) return 0;
      return (costPrice * 100) / salesPrice;
    },
    handleIngredientDeletion(item, key) {
      this.$store.commit("SET_REQUEST_FLAG", "MODAL_REQUEST");
      this.$http
        .get(
          `production/item/ingredients/delete/${this.selectedProduct.id}/${item.ingredient}`
        )
        .then((response) => {
          this.removeIngredient(key);
          if (response.data.deleted) {
            this.currentIngredients.splice(key, 1);
          }
        });
    },
    handleSubmit() {
      this.$http
        .post(
          "branches/item/configure",
          this.$helper.generateFormData({
            product_id: this.selectedProduct.id,
            configurations: this.configurations,
          })
        )
        .then((response) => {
          if (response.data.status) {
            this.toggleModal();
            this.rows[this.configuredIndex] = response.data.product;
            this.configurations = [];
            this.currentIngredients = [];
            this.selectedProduct = null;
            this.configuredIndex = null;
          }
        })
        .catch((err) => console.log(err));
    },
    getItemConfigurations(product, index) {
      this.configurations = [];
      this.currentIngredients = [];
      this.selectedItemIds = [];
      this.selectedProduct = product;
      this.configuredIndex = index;
      this.$http.get(`items/configurations/${product.id}`).then((response) => {
        this.configurations = response.data.rows;
        this.currentIngredients = [...response.data.rows];
        this.currentIngredients.forEach((item) => {
          this.selectedItemIds.push(item.ingredient);
        });
        this.toggleModal();
      });
    },
    selectedIngredient(ingredent) {
      console.log(ingredent);
      this.newItem.product = ingredent;
    },
    addIngredient() {
      if (this.$helper.empty(this.newItem.product)) {
        this.$notifier({
          type: "danger",
          message: "Please select Ingredient",
        });
      } else {
        let product = this.newItem.product;
        this.handleSelectedIngredient({
          ingredient: product.id,
          name: product.name,
          code: product.code,
          quantity: this.newItem.quantity,
          cost: product.cost,
          total_amount: product.cost * this.newItem.quantity,
        });
      }
    },
    removeIngredient(key) {
      this.configurations.splice(key, 1);
      this.selectedItemIds.splice(key, 1);
    },
    handleSelectedIngredient(item) {
      if (!this.selectedItemIds.includes(item.ingredient)) {
        this.configurations.push(item);
        this.selectedItemIds.push(item.ingredient);
        this.$helper.resetObjectValues(this.newItem);
      } else {
        this.$notifier({
          type: "info",
          message: "Ingredient already added to the list",
        });
      }
    },
  },
};
</script>
