<template>
  <content-header
    :title="newItem.id ? 'Edit menu item' : 'Create a new menu'"
    subtitle="Fill out all required information for a menu item"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-body">
          <div class="card-heade">
            <h4 class="mb-3">Item Information</h4>
          </div>
          <div class="row">
            <div class="col-lg-8 col-xl-6 col-sm-10">
              <form class="forms-sample">
                <div class="row mb-3">
                  <label class="col-sm-4 col-xl-3 col-form-label"
                    >Item Name</label
                  >
                  <div class="col-sm-8 col-xl-9">
                    <input
                      type="text"
                      class="form-control"
                      id="default-01"
                      placeholder="Product name"
                      v-model="newItem.name"
                      name="Item Name"
                    />
                  </div>
                </div>

                <div class="row mb-3">
                  <label class="col-sm-4 col-xl-3 col-form-label"
                    >Sales Price</label
                  >
                  <div class="col-sm-8 col-xl-9">
                    <input
                      type="text"
                      class="form-control"
                      id="default-03"
                      :placeholder="`In ${appSettings.currency} (optional)`"
                      v-model="newItem.price"
                      name="Sales Price"
                    />
                  </div>
                </div>

                <div class="row mb-3">
                  <label class="col-sm-4 col-xl-3 col-form-label"
                    >Item Type</label
                  >
                  <div class="col-sm-8 col-xl-9">
                    <select
                      v-model="newItem.type_id"
                      :options="categories"
                      class="form-select"
                    >
                      <option value="null" disabled hidden>Select One</option>
                      <option
                        :value="item.id"
                        v-for="item in categories"
                        :key="item.id"
                      >
                        {{ item.name }}
                      </option>
                    </select>
                  </div>
                </div>

                <div class="row mb-3">
                  <label class="col-sm-4 col-xl-3 col-form-label"
                    >Item Unit</label
                  >
                  <div class="col-sm-8 col-xl-9">
                    <select
                      class="form-control"
                      id="default-06"
                      v-model="newItem.unit_id"
                    >
                      <option value="null" hidden disabled>Select Unit</option>
                      <option
                        :value="item.id"
                        v-for="(item, i) in units"
                        :key="'unit' + i"
                      >
                        {{ item.name }}
                      </option>
                    </select>
                  </div>
                </div>

                <div class="row mb-3">
                  <label class="col-sm-4 col-xl-3 col-form-label"
                    >Has Addons?</label
                  >
                  <div class="col-sm-8 col-xl-9">
                    <div class="form-check form-check-inline">
                      <input
                        id="has-addons-1"
                        class="form-check-input"
                        type="radio"
                        name="member-type"
                        v-model="newItem.has_addons"
                        value="1"
                      />
                      <label for="has-addons-1" class="form-check-label"
                        >Yes</label
                      >
                    </div>
                    <div class="form-check form-check-inline">
                      <input
                        id="has-addons-2"
                        class="form-check-input"
                        type="radio"
                        name="member-type"
                        v-model="newItem.has_addons"
                        value="0"
                      />
                      <label for="has-addons-2" class="form-check-label"
                        >No</label
                      >
                    </div>
                  </div>
                </div>

                <div class="row mb-3">
                  <label class="col-sm-4 col-xl-3 col-form-label"
                    >Description</label
                  >
                  <div class="col-sm-8 col-xl-9">
                    <textarea
                      class="form-control no-resize"
                      id="default-textarea"
                      v-model="newItem.description"
                      placeholder="Description"
                    ></textarea>
                  </div>
                </div>
                <div class="my-1 float-end">
                  <wolf-button
                    class="btn btn-primary"
                    @clicked="handleSubmit"
                    :disabler="!newItem.type_id || !newItem.name"
                    activator="saving_records"
                    >Save</wolf-button
                  >
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
export default {
  name: "ItemCreator",
  components: {
    ContentHeader,
  },
  data: () => ({
    category: {},
    categories: {},
    units: [],
    newItem: {
      name: null,
      description: null,
      cost: null,
      price: null,
      store: null,
      type_id: null,
      unit_id: null,
      quantity: 0,
      daily_count: 0,
      status: "MENU",
      category: null,
      supplier: null,
      store_min_qty: 0,
      store_purchase: 0,
      branch_min_qty: 0,
      branch_purchase: 0,
      source: null,
      has_addons: 0,
    },
    editingMode: false,
  }),
  beforeCreate() {
    let reference = this.$route.params.reference;
    if (reference) {
      this.$http.get(`items/show/${reference}`)
        .then((response) => {
          this.newItem = { ...this.newItem, ...response.data.product };
          this.editingMode = true;
        });
    }
  },
  mounted() {
    this.$http
      .all([this.$http.get("items/extras"), this.$http.get("items/types")])
      .then(
        this.$http.spread((responseOne, responseTwo) => {
          this.categories = responseTwo.data.rows;
          this.units = responseOne.data.units;
        })
      );
  },
  methods: {
    handleSubmit() {
      this.$http
        .post("items/store", this.$helper.generateFormData(this.newItem))
        .then((response) => {
          if (response.data.status) {
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "Item created successfully",
            });
            this.$helper.resetObjectValues(this.newItem);
            this.newItem = {
              name: null,
              description: null,
              cost: null,
              price: null,
              store: null,
              type_id: null,
              unit_id: null,
              quantity: 0,
              daily_count: 0,
              status: "MENU",
              category: null,
              supplier: null,
              store_min_qty: 0,
              store_purchase: 0,
              branch_min_qty: 0,
              branch_purchase: 0,
            };
          }
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>
