<template>
  <div
    class="content__boxed w-100 min-vh-100 d-flex flex-column align-items-center justify-content-center"
  >
    <div class="content__wrap">
      <div class="card shadow-lg">
        <div class="card-body">
          <div class="text-center">
            <img src="/img/logo.png" class="w-50 mb-4" alt="" />
            <h1 class="h3">Front Office Login</h1>
          </div>
          <form class="mt-4" @submit.prevent="authenticateUser">
            <div class="mb-3">
              <input
                type="password"
                class="form-control"
                placeholder="Passcode or PIN number"
                v-model="credentials.password"
                name="password"
                style="-webkit-text-security: circle"
              />
            </div>

            <div class="d-grid mt-2">
              <wolf-button
                type="submit"
                @clicked="authenticateUser"
                :disabler="!credentials.password"
                activator="SIGNING_REQUEST"
                class="btn btn-primary btn-lg"
                >Sign In</wolf-button
              >
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//import { Form, Field, ErrorMessage } from 'vee-validate';
export default {
  name: "login",
  data: () => ({
    credentials: {
      password: null,
    },
    invalidLogin: false,
    loginSucceed: false,
    passwordType: "password",
  }),
  created() {
    this.$store.commit("SET_LOCKED_BLANCH", null);
    this.$store.dispatch("logout");
  },
  methods: {
    handleVisibility() {
      if (this.passwordType == "password") this.passwordType = "text";
      else this.passwordType = "password";
    },
    authenticateUser() {
      this.$http
        .post(
          "auth/authenticate",
          this.$helper.generateFormData(this.credentials)
        )
        .then((response) => {
          if (response.data.status) {
            const token = response.data.access_token || response.data.token;
            if (token) {
              localStorage.setItem("token", token);
            }
            /*this.invalidLogin = false;
            this.loginSucceed = true; */
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "Login Succeed. Redirecting...",
            });
            const redirect = { name: "FrontOfficeHome" };
            if (navigator.userAgent.indexOf("Firefox") > 0)
              setTimeout(() => {
                location.replace(this.$router.resolve({ redirect }).href);
              }, 2000);
            else location.replace(this.$router.resolve(redirect).href);
          }
        })
        .catch((err) => {
          if (err.response.status == 401) {
            /*this.invalidLogin = true;
            this.loginSucceed = false; */
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "danger",
              message: "Invalid Passcode. Try Again",
            });
          }
        });
    },
  },
};
</script>
