<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import ZohoSearch from "@/components/ZohoSearch.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import Avatar from "@/components/Avatar.vue";
import { mapState } from "vuex";
export default {
  name: "WaitersBalanceReport",
  mixins: [dataTableService],
  components: { ContentHeader, ZohoSearch, Avatar },
  data: () => ({
    fetchUrl: "reports/waiters-balance",
    displayMode: true,
    invoices: [],
    ajaxFlag: "GETTING_WAITER_INVOICES",
    selectedWaiter: {},
  }),
  computed: {
    ...mapState({
      requestFlag: (state) => state.requestFlag,
      isLoading: (state) => state.isLoading,
    }),
    isRequesting() {
      return this.isLoading && this.requestFlag === this.ajaxFlag;
    },
  },
  methods: {
    toggleCrudMode() {
      this.displayMode = !this.displayMode;
    },
    handleWaiterFilter(waiter) {
      if (waiter) {
        this.newFilter.waiter = waiter.id;
        this.handlePaginationFilter();
      }
    },
    getWaiterInvoices(waiter) {
      this.invoices = [];
      this.selectedWaiter = waiter;
      this.toggleCrudMode();
      this.$store.commit("SET_REQUEST_FLAG", this.ajaxFlag);
      this.$http
        .get(`orders/waiters/${waiter.id}/assigned`)
        .then((response) => {
          this.invoices = response.data.rows;
        });
    },
  },
};
</script>
<template>
  <content-header
    title="Waiters Balance Report"
    subtitle="Explore waiters balance report and it details throughout period of time"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="crud-container">
          <div class="crud-display" :class="{ empty: !displayMode }">
            <template v-if="displayMode">
              <div class="card-header">
                <div class="row">
                  <div class="col-md-8 d-flex align-items-center mb-1">
                    <div
                      class="date-range d-flex align-items-center"
                      style="width: 200px"
                    >
                      <div class="d-flex align-items-center input-wrapper">
                        <input
                          type="date"
                          v-model="newFilter.from"
                          class="form-control"
                          @click="displayPicker"
                        />
                        <span class="input-separator">-</span>
                        <input
                          type="date"
                          v-model="newFilter.to"
                          class="form-control"
                          @click="displayPicker"
                        />
                      </div>
                      <div class="icon ms-auto">
                        <i class="demo-pli-calendar-4 fs-5"></i>
                      </div>
                    </div>
                    <div class="ms-1">
                      <zoho-search
                        url="users/search?role=waiters"
                        label="name"
                        primary-key="id"
                        placeholder="select a waiter"
                        v-model="newFilter.waiter"
                        :load-defaults="true"
                        fetch-flag="SEARCHING_FOR_WAITERS"
                      ></zoho-search>
                    </div>
                  </div>
                  <div
                    class="col-md-4 d-flex gap-1 align-items-center justify-content-md-end mb-1"
                  >
                    <button
                      class="btn btn-primary"
                      @click="handlePaginationFilter"
                    >
                      <span>Apply</span>
                    </button>
                    <button
                      class="btn btn-icon btn-outline-primary"
                      @click="resetFilters"
                    >
                      <i class="demo-pli-refresh fs-5"></i>
                    </button>
                    <button class="btn btn-icon btn-outline-light">
                      <i class="demo-pli-printer fs-5"></i>
                    </button>
                    <button class="btn btn-icon btn-outline-light">
                      <i class="demo-pli-file-excel fs-5"></i>
                    </button>
                  </div>
                </div>
              </div>
              <div class="card-body">
                <div class="table-responsive">
                  <table class="table table-striped table-sm">
                    <thead>
                      <tr>
                        <th>$/N</th>
                        <th>Avatar</th>
                        <th>Waiter Name</th>
                        <th>Balance</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="row in rows" :key="row.waiter.id">
                        <td>
                          <span>{{ i + 1 }}</span>
                        </td>
                        <td v-if="isSalesReport">
                          <avatar
                            :name="row.waiter ? row.waiter.name : 'Admin'"
                            width="2.25rem"
                            height="2.25rem"
                            :is-large="true"
                          ></avatar>
                        </td>
                        <td>
                          <span class="fw-bolder">
                            {{ row.waiter ? row.waiter.name : "Admin" }}</span
                          >
                        </td>
                        <td>
                          <span class="fw-bolder">{{
                            formatMoney(row.balance)
                          }}</span>
                        </td>

                        <td class="text-end text-nowrap">
                          <button
                            type="button"
                            class="btn btn-icon btn-primary"
                          >
                            <i class="demo-psi-view-list"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <pagination
                  :from="recordsFrom"
                  :to="recordsTo"
                  :current_page="currentPage"
                  :row_count_page="rowCountPage"
                  @page-update="handlePagination"
                  :total_records="totalRows"
                  :page_range="pageRange"
                ></pagination>
              </div>
            </template>
            <template v-else>
              <div class="crud-display-line">
                <button
                  class="btn-back crud-back"
                  type="button"
                  @click="toggleCrudMode"
                >
                  <svg
                    height="18px"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="2"
                      d="M10 19l-7-7m0 0l7-7m-7 7h18"
                    ></path>
                  </svg>
                </button>
                <div class="crud-links"></div>
              </div>
            </template>
          </div>
          <div class="crud-create" :class="{ 'd-none': displayMode }">
            <div class="col-xxl-5 col-xl-6 col-lg-8 col-12">
              <div class="card-header">
                <h3>Track unpaid orders for {{ selectedWaiter.name }}</h3>
              </div>
              <div class="card-body">
                <div class="table-responsive">
                  <table class="table table-striped table-sm">
                    <thead>
                      <tr>
                        <th>Order #</th>
                        <th>Table</th>
                        <th>Amount</th>
                        <th>Invoiced</th>
                        <th>Created at</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="order in invoices" :key="order.reference">
                        <td>
                          <span
                            >#{{ $helper.generateVoucherNo(order.id) }}</span
                          >
                        </td>
                        <td>
                          <a class="btn-link" href="javascript:void(0)">{{
                            order.table.name
                          }}</a>
                        </td>

                        <td>
                          <span class="fw-bolder">{{
                            formatMoney(order.grand_total)
                          }}</span>
                        </td>
                        <td>
                          <span
                            class="fw-bolder text-success"
                            v-if="order.printed == 1"
                            >YES</span
                          >
                          <span class="fw-bolder text-warning" v-else>NO</span>
                        </td>
                        <td>
                          <span>{{
                            `${$helper.formatDate(
                              order.created_at
                            )} ${$helper.formatOrderTime(order.created_at)}`
                          }}</span>
                        </td>
                        <td class="text-end text-nowrap">
                          <div class="">
                            <button
                              class="btn btn-icon btn-xs btn-light"
                              data-bs-toggle="dropdown"
                              aria-expanded="false"
                            >
                              <i class="demo-pli-dot-horizontal"></i>
                              <span class="visually-hidden"
                                >Toggle Dropdown</span
                              >
                            </button>
                            <ul
                              class="dropdown-menu dropdown-menu-end"
                              style=""
                            >
                              <li>
                                <a
                                  class="dropdown-item"
                                  href="javascript:void(0)"
                                  ><svg
                                    height="16px"
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="0 0 24 24"
                                    fill="none"
                                    stroke="currentColor"
                                    stroke-width="2"
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                    class="me-1"
                                  >
                                    <path
                                      d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"
                                    ></path>
                                    <circle cx="12" cy="12" r="3"></circle></svg
                                  >View Items</a
                                >
                              </li>

                              <li>
                                <a
                                  class="dropdown-item"
                                  href="javascript:void(0)"
                                  @click.prevent="
                                    $confirm({
                                      title: 'Delete invoice record',
                                      content: `Are you sure you want to delete this invoice #${$helper.generateVoucherNo(
                                        order.id
                                      )} ?`,
                                      classes: 'btn btn-danger',
                                      actionText: 'Yes, Continue',
                                      closeText: 'Cancel',
                                      action: () => {
                                        handleDestroy(order.id);
                                      },
                                    })
                                  "
                                  ><i class="demo-pli-trash fs-5 me-1"></i
                                  >Delete</a
                                >
                              </li>
                            </ul>
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
